import { Component, OnInit } from '@angular/core';
import { GetTenantsService } from 'src/app/services/Tenants/getTenantsService';
import { Router, ActivatedRoute, NavigationEnd } from "@angular/router";
import { Location } from "@angular/common";
import { IonRouterOutlet, NavController, ModalController } from '@ionic/angular';
import {  IGetMainMedicalServiceForViewDto } from 'src/app/models/MainMedicalService';
import { LoadingService } from 'src/app/services/loading/loading.service';
import { DataShareService } from 'src/app/services/DataShare/data-share.service';
import { AdvancedFiterComponent } from 'src/app/components/advanced-fiter/advanced-fiter.component';
import { pageName } from 'src/app/enums/pageName';

@Component({
  selector: 'app-service-provider',
  templateUrl: './service-provider.page.html',
  styleUrls: ['./service-provider.page.scss'],
})
export class ServiceProviderPage implements OnInit {


  //#region  back button variables 
    private routerEvents : any;
    private previousUrl : string;
    private currentUrl : string;
    private canGoBack : boolean;
    //#endregion

  mainServiceId;
  mainMedicalServiceData : IGetMainMedicalServiceForViewDto;

  ServiceProviders =[];

  spinnerShow : boolean = true ;

  constructor(
    private _providersService: GetTenantsService,
    private activeRouter: ActivatedRoute,
    private location: Location,
    private ionRouterOutlet:IonRouterOutlet,
    private router : Router,
    private _ctrloading : LoadingService,
    private _dataShare : DataShareService ,
    private navctrl : NavController ,
    private modalController: ModalController
  ) {
    // this.activeRouter.queryParams.subscribe(mainServiceId => {
    //   this.mainServiceId = mainServiceId;
    //   // console.log(this.mainServiceId)
    // })

  }

  ngOnInit() {}
  ionViewWillEnter()
  {
    this.initialSetting();
    this.getAllServiceProviders(this.mainServiceId , undefined);
    
  }

  initialSetting(){
    
    if (this.activeRouter.snapshot.data['dataSharing']) {

      this.mainMedicalServiceData = this.activeRouter.snapshot.data['dataSharing'];
      console.log(this.mainMedicalServiceData)//get data from mainServiceData page
      this.mainServiceId = this.mainMedicalServiceData.mainMedicalService.id;
      
      console.log( this.mainServiceId )

    }    
  }


  getAllServiceProviders(mainServiceId: number,filter:string) {

    //this._ctrloading.present();
    this.spinnerShow = true;
    this._providersService.getAllServiceProviders(mainServiceId , filter).subscribe((data:any) => {
      console.log(data.result)
      data.result.items.forEach(element =>{
        this.ServiceProviders.push(element);
       // this._ctrloading.dismiss();
       this.spinnerShow = false;
      })
      console.log(this.ServiceProviders)
    },error =>{
      this.spinnerShow = false;
      console.log(error)
    });

  }

  viewProviders(serviceProvider){
    console.log("viewProviders")
    this._dataShare.setData("serviceProvider" , serviceProvider);
    console.log(serviceProvider);
    this.router.navigateByUrl("tabs/provided-services/serviceProvider")
  }

 
  ionViewWillLeave(){
    this.ServiceProviders = [];
    this.mainServiceId = 0;
  }


  //#region back button function
  previousPage() {
    this.location.back();
    console.log(this.location.back())

    this.canGoBack = this.ionRouterOutlet.canGoBack();
    this.currentUrl = this.router.url;
    this.routerEvents = this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
        this.previousUrl = this.currentUrl;
        this.currentUrl = event.url;
      }
    })
       console.log( this.previousUrl )

  }
  //#endregion

  PreviousPage(){
    this.navctrl.back();
   // console.log(this.location.path());
  }

  async filterModal() {
    const modal = await this.modalController.create({
    component: AdvancedFiterComponent,
    componentProps: { 
      filtrationList : this.ServiceProviders,
      filterPageName : pageName.SERVICEPROVIDER,
      patientHidden : true,
      SearchByNameHidden:false,
      priceHidden : true

     }

    });

  
    await modal.present();
    await modal.onWillDismiss().then((data)=>{
      console.log(data.data);
      if(data.data.searchList){
      this.ServiceProviders=[]
      this.getAllServiceProviders(this.mainServiceId , data.data.searchList.name);
      }
    })
    
  
  }

}
